/*================================================
Default CSS
=================================================*/
@import url("https://fonts.googleapis.com/css?family=Poppins:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");

$font-family: "Poppins", sans-serif;
$main-color: #ff4800;
$paragraph-color: #848484;
$paragraph-color2: #d6d6d6;
$white-color: #ffffff;
$black-color: #000000;
$sub-black-color: #111111;
$transition: 0.5s;
$font-size: 15px;

body {
    font-family: $font-family;
    padding: 0;
    margin: 0;
    font-size: $font-size;
}
p {
    font: {
        size: $font-size;
    }
    color: $paragraph-color;
    line-height: 1.9;
    margin: {
        bottom: 15px;
        top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}
img {
  max-width: 100%;
}
.uk-h1,
.uk-h2,
.uk-h3,
.uk-h4,
.uk-h5,
.uk-h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font: {
    weight: 500;
    family: $font-family;
  }
  margin: {
    top: 15px;
    bottom: 15px;
  }
  color: $black-color;
}
a {
  outline: 0 !important;
  transition: $transition;
  display: block;

  &:hover,
  &:focus {
    text-decoration: none;
    outline: 0;
  }
}
.ptb-100 {
  padding: {
    top: 100px;
    bottom: 100px;
  }
}
.d-table {
  width: 100%;
  display: table;
  height: 100%;

  &-cell {
    vertical-align: middle;
    display: table-cell;
  }
}
.bg-gray {
  background-color: #f4f4f4;
}
.uk-dark {
  background-color: $black-color;
}
/*uk-default-btn*/
.uk-button-default {
  border: 2px solid $main-color;
  background-color: $main-color;
  color: $white-color;

  &:hover,
  &:focus {
    background: $white-color;
    color: $white-color;
    border-color: $main-color;
  }
  i {
    margin-left: 3px;
  }
}
.uk-button {
  font: {
    size: 15px;
    weight: 600;
  }
  line-height: unset;
  text-transform: capitalize;
  padding: 13px 32px;
  transition: $transition;
  position: relative;
  z-index: 1;

  &::before,
  &::after {
    content: "";
    position: absolute;
    height: 0;
    width: 51%;
    background: $main-color;
    z-index: -1;
    transition: $transition;
  }
  &::after {
    left: 0;
    top: 0;
  }
  &::before {
    right: 0;
    bottom: 0;
  }
  &:hover,
  &:focus {
    &::before,
    &::after {
      height: 100%;
    }
  }
}
/*section-title*/
.section-title {
  margin-bottom: 60px !important;
  position: relative;

  .bar {
    position: relative;
    z-index: 1;
    background: $main-color;
    width: 55px;
    height: 3px;
    margin: {
      top: 12px;
    }
    transition: $transition;

    &::before,
    &::after {
      content: "";
      position: absolute;
      height: 100%;
      width: 3px;
      top: 0;
      background: $white-color;
      transition: $transition;
    }
    &::after {
      right: 10px;
    }
    &::before {
      right: 20px;
    }
  }
  span {
    margin-bottom: -5px;
    color: $main-color;
    display: block;
    text-transform: uppercase;
  }
  h2 {
    font: {
      size: 34px;
      weight: 700;
    }
  }
  .uk-button {
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -25px;
  }
}
/*uk-input*/
.uk-input,
.uk-select:not([multiple]):not([size]) {
  height: 47px;
}
.uk-input,
.uk-select,
.uk-textarea {
  padding: 10px;
  transition: $transition;

  &:focus {
    border-color: $main-color;
    padding-left: 15px;
  }
}

/*================================================
Preloader CSS
=================================================*/
.uk-preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999999;
  background: $main-color;
  left: 0;
  right: 0;

  .spinner {
    width: 70px;
    height: 70px;
    position: absolute;
    margin: 0 auto;
    right: 0;
    left: 0;
    top: 50%;
    margin-top: -70px;

    .double-bounce1,
    .double-bounce2 {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: $white-color;
      position: absolute;
      top: 0;
      left: 0;
      animation: sk-bounce 2s infinite ease-in-out;
    }
    .double-bounce2 {
      opacity: 0.8;
      animation-delay: -1s;
    }
  }
}
@keyframes sk-bounce {
  0%,
  100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}

/*================================================
Header CSS
=================================================*/
.header-area {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
  z-index: 999;
  padding: {
    top: 60px;
    bottom: 35px;
  }

  .uk-navbar-container {
    &:not(.uk-navbar-transparent) {
      background: transparent;
    }
  }
  &.uk-sticky-fixed {
    padding: {
      top: 30px;
      bottom: 30px;
    }
    &.uk-sticky-below {
      background: $white-color;
    }
  }
  #navbar-toggle {
    position: absolute;
    right: 0;
    top: 4px;
    width: 35px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    cursor: pointer;
    padding: 0;
    height: 20px;
    min-height: 20px;
    display: none;

    span {
      display: block;
      width: 100%;
      height: 3px;
      background-color: $black-color;
    }
    * + span {
      margin-top: 3px;
    }
  }
}
.navbar {
    .uk-navbar-nav {
        li {
            padding: 0 15px;

            a {
                color: $black-color;
                text-transform: capitalize;
                transition: $transition;
                padding: 0;
                position: relative;
                min-height: 25px;
                z-index: 1;
                font: {
                    size: $font-size;
                    weight: 600;
                    family: $font-family;
                }
                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    right: 0;
                    margin: 0 auto;
                    background: $main-color;
                    width: 0;
                    height: 2px;
                    z-index: -1;
                    opacity: 0;
                    visibility: hidden;
                    transition: $transition;
                }
                &:hover, &:focus, &.active {
                    color: $main-color;

                    &::before {
                        opacity: 1;
                        visibility: visible;
                        width: 100%;
                    }
                }
            }
            .uk-dropdown {
                padding: 15px 5px 15px 0;
                display: block;
                opacity: 0;
                visibility: hidden;
                transition: $transition;
                top: 120% !important;

                .uk-dropdown-nav {
                    li {
                        margin-bottom: 8px;

                        a {
                            color: $black-color;

                            &::after {
                                position: absolute;
                                left: -15px;
                                top: 0;
                                height: 0;
                                width: 3px;
                                background: $main-color;
                                content: '';
                                opacity: 0;
                                visibility: hidden;
                                transition: $transition;
                            }
                            &::before {
                                display: none;
                            }
                            &:hover, &:focus {
                                color: $main-color;

                                &::after {
                                    opacity: 1;
                                    height: 100%;
                                    visibility: visible;
                                }
                            }
                        }
                        &.uk-active {
                            a {
                                color: $main-color;

                                &::after {
                                    opacity: 1;
                                    visibility: visible;
                                    height: 100%;
                                }
                            }
                        }
                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }
            }
            &:hover {
                .uk-dropdown {
                    opacity: 1;
                    visibility: visible;
                    top: 95% !important;
                }
            }
            &.uk-active {
                a {
                    color: $main-color;

                    &::before {
                        opacity: 1;
                        visibility: visible;
                        width: 100%;
                    }
                }
            }
        }
    }
    .lang {
        margin-left: 6px;

        form {
            select {
                background: transparent;
                border: none;
                font: {
                    size: $font-size;
                    weight: 600;
                }
                color: $black-color;
                outline: 0 !important;
                cursor: pointer;
                box-shadow: unset !important;
            }
        }
    }
}
.uk-sticky-placeholder {
    height: 0 !important;
}
/*Mobile Navbar*/
