    z-index: 99999;

    .uk-navbar-container {
        &:not(.uk-navbar-transparent) {
            background: transparent;
        }
    }
    .uk-offcanvas-bar {
        width: 290px;
        padding: 40px;
        background-color: $black-color;
    }
    .uk-navbar-nav {
        display: block;

        li {
            padding: 10px 0;
            display: block;

            a {
                color: $white-color;
                text-transform: capitalize;
                transition: $transition;
                font: {
                    size: $font-size;
                    weight: 600;
                    family: $font-family;
                }
                padding: 0;
                display: inline-block;
                position: relative;
                min-height: 25px;
                z-index: 1;

                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    right: 0;
                    margin: 0 auto;
                    background: $main-color;
                    width: 0;
                    height: 2px;
                    z-index: -1;
                    opacity: 0;
                    visibility: hidden;
                    transition: $transition;
                }
                &:hover, &:focus {
                    color: $main-color;

                    &::before {
                        opacity: 1;
                        visibility: visible;
                        width: 100%;
                    }
                }
            }
            .uk-dropdown {
                padding: 15px 5px 15px 0;

                .uk-dropdown-nav {
                    li {
                        margin-bottom: 8px;
                        padding: 0 15px;

                        a {
                            color: $black-color;

                            &::after {
                                position: absolute;
                                left: -15px;
                                top: 0;
                                height: 0;
                                width: 3px;
                                background: $main-color;
                                content: '';
                                opacity: 0;
                                visibility: hidden;
                                transition: $transition;
                            }
                            &::before {
                                display: none;
                            }
                            &:hover, &:focus {
                                color: $main-color;

                                &::after {
                                    opacity: 1;
                                    height: 100%;
                                    visibility: visible;
                                }
                            }
                        }
                        &.uk-active {
                            a {
                                color: $main-color;

                                &::after {
                                    opacity: 1;
                                    visibility: visible;
                                    height: 100%;
                                }
                            }
                        }
                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }
            }
            &.uk-active {
                a {
                    color: $main-color;

                    &::before {
                        opacity: 1;
                        visibility: visible;
                        width: 100%;
                    }
                }
            }
        }
    }
}

/*================================================
Main Banner CSS
=================================================*/
.main-banner {
  height: 750px;
  background: {
    position: right;
    repeat: no-repeat;
    size: 575px;
    position: 92%;
  }
  position: relative;
  z-index: 1;
}
.item-bg1 {
  background-image: url(../images/man1.png);
}
.item-bg2 {
  background-image: url(../images/beard-man.png);
}
.item-bg3 {
  background-image: url(../images/main-women.png);
}
.main-banner-content {
  max-width: 560px;

  h1 {
    font: {
      size: 65px;
      weight: 700;
    }
    position: relative;
    z-index: 1;
    line-height: 63px;
    padding: 8px 0;
    margin: 0;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 30px;
      height: 100%;
      z-index: -1;
      background: $main-color;
    }
  }
  p {
    margin: {
      top: 30px;
      bottom: 30px;
    }
  }
  a {
    display: inline-block;
  }
  .video-btn {
    margin-left: 85px;
    color: $paragraph-color;
    font-weight: 600;

    .uk-icon {
      width: 45px;
      margin-right: 5px;
      height: 45px;
      line-height: 45px;
      background-color: $white-color;
      border: 2px solid $main-color;
      border-radius: 50%;
      text-align: center;
      color: $main-color;
      transition: $transition;

      svg {
        width: 30px;
      }
    }
    &:hover,
    &:focus {
      color: $main-color;

      .uk-icon {
        background-color: $black-color;
        color: $white-color;
        border-color: $black-color;
      }
    }
  }
}
.banner-with-form {
    .main-banner-content {
        max-width: unset;
    }
    .uk-container {
        .uk-grid {
            margin-top: 30px;
        }
    }
}
.main-banner-form {
    border: 3px solid #111111;
    background: $black-color;
    padding: 35px;
    box-shadow: 0 2px 48px 0 rgba(0, 0, 0, 0.08);
    margin-left: 45px;

    form {
        select {
            &.uk-input {
                cursor: pointer;
                line-height: initial;
            }
        }
        label {
            span {
                color: #8a8a8a;
                position: relative;
                top: -3px;
                font-size: 13px;

                a {
                    display: inline-block;
                    color: $main-color;
                }
            }
        }
    }
}

/*================================================
Features CSS
=================================================*/
.features-area {
  position: relative;
  margin-top: -50px;
  z-index: 1;
  background: $white-color;

  &.uk-dark {
    background-color: $black-color;
  }
}
.single-features-box {
    background-color: #f7f7ff;
    position: relative;
    z-index: 1;
    padding: 55px 40px;
    transition: $transition;

    .icon {
        color: $main-color;
        transition: $transition;

        i {
            &::before {
                font-size: 45px;
            }
        }
    }
    h3 {
        font: {
            size: 22px;
            weight: 700;
        }
        margin-bottom: 0;
        transition: $transition;
    }
    .dot-img {
        position: absolute;
        right: 5px;
        bottom: 5px;
        z-index: -1;
        transition: $transition;

        img {
            transition: $transition;

            &.white-dot {
                display: none;
            }
        }
    }
    .bar {
        position: relative;
        z-index: 1;
        background: $main-color;
        width: 55px;
        height: 3px;
        transition: $transition;
        margin: {
            top: 7px;
            bottom: 20px;
        }

        &::before, &::after {
            content: "";
            position: absolute;
            height: 100%;
            width: 3px;
            top: 0;
            background: $white-color;
            transition: $transition;
        }
        &::after {
            right: 10px;
        }
        &::before {
            right: 20px;
        }
    }
    p {
        margin-bottom: 0;
        transition: $transition;
    }
    .animation-img {
        img {
            position: absolute;
            opacity: 0;
            visibility: hidden;
            transition: $transition;

            &:nth-child(1) {
                left: 0;
                bottom: 0;
            }
            &:nth-child(2) {
                top: 50%;
                transform: translateY(-50%);
                left: 0;
            }
            &:nth-child(3) {
                top: 0;
                left: 0;
            }
            &:nth-child(4) {
                left: 50%;
                transform: translateX(-50%);
                top: 0;
            }
            &:nth-child(5) {
                right: 0;
                top: 0;
            }
            &:nth-child(6) {
                top: 50%;
                transform: translateY(-50%);
                right: 0;
            }
            &:nth-child(7) {
                bottom: 0;
                right: 0;
            }
            &:nth-child(8) {
                left: 50%;
                transform: translateX(-50%);
                bottom: 0;
            }
        }
    }
    &:hover, &:focus, &.active {
        background: $main-color;

        .icon {
            color: $white-color;
        }
        h3 {
            color: $white-color;
        }
        p {
            color: $white-color;
        }
        .dot-img {
            img {
                &.color-dot {
                    display: none;
                }
                &.white-dot {
                    display: block;
                }
            }
        }
        .bar {
            background: $white-color;

            &::after, &::before {
                background: $main-color;
            }
        }
    }
    &:hover, &:focus {
        .animation-img {
            img {
                opacity: 1;
                visibility: visible;

                &:nth-child(1) {
                    left: -25px;
                    bottom: -24px;
                }
                &:nth-child(2) {
                    left: -35px;
                }
                &:nth-child(3) {
                    top: -15px;
                    left: -16px;
                }
                &:nth-child(4) {
                    top: -28px;
                }
                &:nth-child(5) {
                    right: -25px;
                    top: -26px;
                }
                &:nth-child(6) {
                    right: -25px;
                }
                &:nth-child(7) {
                    bottom: -26px;
                    right: -25px;
                }
                &:nth-child(8) {
                    bottom: -25px;
                } 
            }
        }
    }
}
.br-line {
  position: relative;
  overflow: hidden;
  height: 60px;
  width: 3px;
  background: $main-color;
  right: 0;
  left: 0;
  margin: 0 auto;
  z-index: 1;

  &::after,
  &::before {
    content: "";
    position: absolute;
    background: $white-color;
    width: 100%;
    height: 3px;
    left: 0;
    z-index: -1;
    animation-duration: 3s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-name: MOVE-BG;
  }
  &::after {
    top: 5px;
  }
  &::before {
    top: 15px;
  }
}
@keyframes MOVE-BG {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(60px);
  }
}

/*================================================
About CSS
=================================================*/
.about-area {
  position: relative;
  overflow: hidden;
}
.about-content {
  .section-title {
    margin-bottom: 40px !important;
  }
  .about-text {
    position: relative;
    padding-left: 65px;

    .icon {
      position: absolute;
      left: 0;
      top: 0;
      color: $main-color;

      i::before {
        font-size: 45px;
      }
    }
    h3 {
      margin-top: 0;
      font-size: 20px;
    }
    .signature {
      margin-top: 35px;
    }
  }
}
.about-img {
  position: absolute;
  right: 0;
  top: 70px;
  width: auto !important;
  text-align: right;

  img {
    transition: $transition;
  }
  .about-img1 {
    width: 85%;
  }
  .about-img2 {
    position: absolute;
    left: 70px;
    bottom: -82px;
  }
  .shape-img {
    position: absolute;
    animation: moveleftbounce 3s linear infinite;
    left: 44%;
    top: 9%;
  }
  .uk-button {
    background-color: $main-color;
    color: $white-color;
    position: absolute;
    right: 0;
    bottom: -25px;

    &:hover,
    &:focus {
      background-color: $black-color;
    }
  }
  &:hover,
  &:focus {
    img {
      filter: grayscale(100%);
    }
  }
}
@keyframes moveleftbounce {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX(0);
  }
}

/*================================================
Services CSS
=================================================*/
.services-area {
    padding-bottom: 40px;

    .item {
        margin-bottom: 30px;
    }
    .pagination-area {
        margin: {
            top: 20px;
            bottom: 30px !important;
        }
    }
}
.single-services {
  position: relative;
  z-index: 1;
  background-color: #f9f9f9;
  padding: 40px 55px 40px 108px;
  transition: $transition;
  border-radius: 65px 0 0 65px;

  .icon {
    position: absolute;
    left: 15px;
    top: 50%;
    z-index: -1;
    width: 75px;
    height: 75px;
    background: $white-color;
    display: inline-block;
    text-align: center;
    border-radius: 50%;
    color: $main-color;
    line-height: 75px;
    margin-top: -38px;
    transition: $transition;

    i::before {
      font-size: 35px;
    }
  }
  h3 {
    font: {
      size: 18px;
      weight: 700;
    }
    margin: 0;
    transition: $transition;

    a {
      color: $black-color;
    }
  }
  .link-btn {
    position: absolute;
    right: 20px;
    top: 50%;
    color: #bdbdbd;
    display: block;
    margin-top: -19px;
    transition: $transition;

    &::before {
      font-size: 25px;
    }
  }
  &:hover,
  &:focus,
  &.active {
    background: $main-color;

    h3,
    a {
      color: $white-color;
    }
    .link-btn {
      right: 10px;
      color: $white-color;
    }
  }
}
.single-services-box {
    background-color: #f7f7ff;
    position: relative;
    z-index: 1;
    padding: 40px;
    transition: $transition;

    .icon {
        color: $main-color;
        transition: $transition;

        i {
            &::before {
                font-size: 45px;
            }
        }
    }
    h3 {
        margin-bottom: 0;
        transition: $transition;
        font: {
            size: 20px;
            weight: 700;
        }
        a {
            color: $black-color;
        }
    }
    .bar {
        position: relative;
        z-index: 1;
        background: $main-color;
        width: 55px;
        height: 3px;
        transition: $transition;
        margin: {
            top: 7px;
            bottom: 20px;
        }

        &::before, &::after {
            content: "";
            position: absolute;
            height: 100%;
            width: 3px;
            top: 0;
            background: $white-color;
            transition: $transition;
        }
        &::after {
            right: 10px;
        }
        &::before {
            right: 20px;
        }
    }
    p {
        margin-bottom: 20px;
        transition: $transition;
    }
    .link-btn {
        display: inline-block;
        position: relative;
        color: $white-color;

        span {
            opacity: 0;
            visibility: hidden;
            transition: $transition;
        }
        i {
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            background-color: $main-color;
            color: $white-color;
            width: 30px;
            height: 30px;
            text-align: center;
            line-height: 30px;
            border-radius: 50%;
            transition: $transition;
        }
        &:hover, &:focus {
            span {
                opacity: 1;
                visibility: visible;
                padding-right: 5px;
            }
            i {
                left: 100%;
                width: 25px;
                height: 25px;
                line-height: 23px;

                &::before {
                    font-size: 12px;
                }
            }
        }
    }
    .animation-img {
        img {
            position: absolute;
            opacity: 0;
            visibility: hidden;
            transition: $transition;

            &:nth-child(1) {
                left: 0;
                bottom: 0;
            }
            &:nth-child(2) {
                top: 50%;
                transform: translateY(-50%);
                left: 0;
            }
            &:nth-child(3) {
                top: 0;
                left: 0;
            }
            &:nth-child(4) {
                left: 50%;
                transform: translateX(-50%);
                top: 0;
            }
            &:nth-child(5) {
                right: 0;
                top: 0;
            }
            &:nth-child(6) {
                top: 50%;
                transform: translateY(-50%);
                right: 0;
            }
            &:nth-child(7) {
                bottom: 0;
                right: 0;
            }
            &:nth-child(8) {
                left: 50%;
                transform: translateX(-50%);
                bottom: 0;
            }
        }
    }
    &:hover, &:focus {
        background: $main-color;

        .icon {
            color: $white-color;
        }
        h3 {
            a {
                color: $white-color;
            }
        }
        p {
            color: $white-color;
        }
        .bar {
            background: $white-color;

            &::after, &::before {
                background: $main-color;
            }
        }
        .link-btn {
            span {
                opacity: 1;
                visibility: visible;
                padding-right: 7px;
            }
            i {
                left: 100%;
                width: 25px;
                height: 25px;
                line-height: 23px;
                background-color: $white-color;
                color: $main-color;

                &::before {
                    font-size: 12px;
                }
            }
        }
        .animation-img {
            img {
                opacity: 1;
                visibility: visible;

                &:nth-child(1) {
                    left: -25px;
                    bottom: -24px;
                }
                &:nth-child(2) {
                    left: -35px;
                }
                &:nth-child(3) {
                    top: -15px;
                    left: -16px;
                }
                &:nth-child(4) {
                    top: -28px;
                }
                &:nth-child(5) {
                    right: -25px;
                    top: -26px;
                }
                &:nth-child(6) {
                    right: -25px;
                }
                &:nth-child(7) {
                    bottom: -26px;
                    right: -25px;
                }
                &:nth-child(8) {
                    bottom: -25px;
                } 
            }
        }
    }
}

