/*================================================
Project CSS
=================================================*/
.project-area {
    overflow: hidden;

    .pagination-area {
        margin: {
            top: 20px;
        }
    }
}
.project-slides {
  left: calc((100% - 1200px) / 2);
  position: relative;
}
.single-project {
  text-align: center;
  margin-bottom: 30px;

  .project-img {
    img {
      transition: $transition;
    }
  }
  .project-content {
    background-color: #f9f9f9;
    padding: 35px 15px;
    transition: $transition;
    position: relative;
    z-index: 1;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 0;
      transition: $transition;
      background: $main-color;
      z-index: -1;
    }
    h3 {
      font: {
        size: 22px;
        weight: 700;
      }
      margin: 0;
      transition: $transition;

      a {
        color: $black-color;

        &:hover,
        &:focus {
          color: $main-color;
        }
      }
    }
    ul {
      padding: 0;
      margin: 8px 0 0;
      list-style-type: none;

      li {
        display: inline-block;
        position: relative;
        margin: 0 8px;
        transition: $transition;

        &::before {
          content: "";
          position: absolute;
          left: -11px;
          top: 0;
          height: 15px;
          width: 2px;
          background: $paragraph-color;
          top: 3px;
          transform: rotate(10deg);
          transition: $transition;
        }
        &:first-child {
          &::before {
            display: none;
          }
        }
        a {
          color: $paragraph-color;

          &:hover,
          &:focus {
            color: $main-color;
          }
        }
      }
    }
  }
  &:hover,
  &:focus {
    .project-content {
      &::before {
        height: 100%;
      }
      h3 {
        a {
          color: $white-color;
        }
      }
      ul {
        li {
          a {
            color: $white-color;
          }
          &::before {
            background: $white-color;
          }
        }
      }
    }
    .project-img {
      img {
        filter: grayscale(100%);
      }
    }
  }
}
.project-slides {
  &.owl-theme {
    .owl-dots {
      line-height: 0.01;
      margin-top: 20px !important;

      .owl-dot {
        span {
          width: 15px;
          height: 5px;
          margin: 0 3px;
          background: #d6d6d6;
          transition: $transition;
          border-radius: 0;
        }
        &.active,
        &:hover,
        &:focus {
          outline: 0 !important;
          box-shadow: unset !important;

          span {
            background: $main-color;
          }
        }
        &.active {
          span {
            width: 22px;
          }
        }
      }
    }
  }
}

