/*================================================
Contact CSS
=================================================*/
.contact-area {
  padding-bottom: 100px;

  .section-title {
    text-align: center;

    h2 {
      margin-bottom: 0;
    }
  }
}
.map-img {
  position: relative;
  z-index: 1;

  .location {
    position: absolute;
    left: 24%;
    top: 30%;
    width: auto;
    height: auto;

    a {
      width: 14px;
      height: 14px;
      background: $main-color;
      border-radius: 50%;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        left: -4px;
        bottom: -4px;
        border: 4px solid #eec3b3;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        animation-name: ripple;
        animation-duration: 2s;
        animation-delay: 0s;
        animation-iteration-count: infinite;
        animation-timing-function: cubic-bezier(0.65, 0, 0.34, 1);
      }
      .location-info {
        position: absolute;
        left: -55px;
        bottom: 30px;
        transition: $transition;
        width: 160px;
        background: $white-color;
        text-align: center;
        padding: 20px 15px;
        border-radius: 3px;
        z-index: 1;
        box-shadow: 0 -2px 20px 0 rgba(0, 0, 0, 0.06);
        opacity: 0;
        visibility: hidden;

        h5 {
          font: {
            size: 16px;
            weight: 700;
          }
          margin: 0;
          color: #616161;
        }
        span {
          display: block;
          font-size: 14px;
          color: #616161;
          margin-top: 6px;
        }
        &::before {
          content: "";
          position: absolute;
          left: 50px;
          bottom: -10px;
          width: 25px;
          height: 25px;
          background: $white-color;
          z-index: -1;
          transform: rotate(45deg);
          box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.06);
        }
      }
      &:hover,
      &:focus,
      &.active {
        .location-info {
          opacity: 1;
          visibility: visible;
          bottom: 40px;
        }
      }
    }
    &.uk-location2 {
      right: 22%;
      left: auto;
      top: 25%;
    }
  }
}
