        background-color: $white-color;
      }
    }
    &.uk-dark {
      background-color: transparent;

      &.uk-sticky-fixed {
        background-color: $black-color;
      }
    }
  }
  .navbar {
    .uk-navbar-nav {
      li {
        a {
          color: $white-color;

          &:hover,
          &:focus {
            color: $main-color;

            &::before {
              opacity: 1;
              visibility: visible;
              width: 100%;
            }
          }
        }
        &.uk-active {
          a {
            color: $main-color;

            &::before {
              opacity: 1;
              visibility: visible;
              width: 100%;
            }
          }
        }
      }
    }
    .lang {
      form {
        select {
          color: $white-color;
          background-color: $black-color;
        }
      }
    }
  }

  .main-banner-content {
    .video-btn {
      .uk-icon {
        background-color: $black-color;
      }
      &:hover,
      &:focus {
        .uk-icon {
          background-color: $white-color;
          color: $black-color;
          border-color: $white-color;
        }
      }
    }
  }

  .single-features-box {
    background-color: $sub-black-color;

    .bar {
      &::before,
      &::after {
        background: $sub-black-color;
      }
    }
    &.active,
    &:focus,
    &:hover {
      background-color: $main-color;

      .bar {
        &::before,
        &::after {
          background: $main-color;
        }
      }
    }
  }

  .section-title {
    .bar {
      &::before,
      &::after {
        background: $black-color;
      }
    }
  }

  .single-services {
    background-color: $sub-black-color;

    .icon {
      background-color: $black-color;
    }
    h3 {
      a {
        color: $white-color;
      }
    }
    &.active,
    &:focus,
    &:hover {
      background-color: $main-color;

      .icon {
        background-color: $white-color;
      }
    }
  }

  .about-img {
    img {
      filter: grayscale(100%);
      transition: $transition;
    }
    .shape-img {
      filter: unset;
    }
    &:hover,
    &:focus {
      img {
        filter: unset;
      }
    }
  }

  .single-project {
    .project-img {
      img {
        transition: $transition;
        filter: grayscale(100%);
      }
    }
    .project-content {
      background-color: $sub-black-color;

      h3 {
        a {
          color: $white-color;
        }
      }
      ul {
        li {
          a {
            color: $paragraph-color2;
          }
        }
      }
    }
    &:hover,
    &:focus {
      .project-content {
        ul {
          li {
            a {
              color: $white-color;
            }
          }
        }
      }
      .project-img {
        img {
          filter: unset;
        }
      }
    }
  }

  .feedback-img {
    img {
      filter: grayscale(100%);
      transition: $transition;

      &.shape-img {
        filter: unset;
      }
    }
    &:hover,
    &:focus {
      img {
        filter: unset;
      }
    }
  }
  .feedback-slides {
    &.owl-theme {
      .owl-nav {
        [class*="owl-"] {
          color: $white-color;

          &:hover,
          &:focus {
            color: $main-color;
          }
        }
      }
    }
  }

  &.partner-area {
    border-bottom-color: $sub-black-color;
  }

  .single-team {
    .team-content {
      background-color: $black-color;

      span {
        color: $paragraph-color2;
      }
    }
    img {
      filter: grayscale(100%);
      transition: $transition;
    }
    &:hover,
    &:focus {
      img {
        filter: unset;
      }
    }
  }

  &.subscribe-area {
    background-color: $sub-black-color;
  }

  .single-blog-post {
    .blog-post-image {
      img {
        filter: grayscale(100%);
        transition: $transition;
      }
    }
    .blog-post-content {
      background-color: $sub-black-color;
      border: none;

      h3 {
        a {
          color: $white-color;

          &:hover,
          &:focus {
            color: $main-color;
          }
        }
      }
    }
    &:hover,
    &:focus {
      .blog-post-image {
        img {
          filter: unset;
        }
      }
    }
  }

  .map-img {
    .location {
      a {
        .location-info {
          background-color: $sub-black-color;

          h5 {
            color: $white-color;
          }
          span {
            color: $paragraph-color2;
          }
          &::before {
            background: $sub-black-color;
          }
        }
      }
    }
  }

  &.footer-area {
    background-color: $sub-black-color;
  }
  .single-footer-widget {
    .social {
      li {
        margin-right: 7px;
        a {
          color: #999999;
          background: $sub-black-color;

          &:hover,
          &:focus {
            background-color: $main-color;
            color: $white-color;
          }
        }
      }
    }
  }

  #contactForm {
    .uk-input,
    .uk-textarea {
      color: $white-color;
      background-color: $sub-black-color;
    }
  }

  .br-line {
    &::after,
    &::before {
      background: $black-color;
    }
  }
}